/****   request.js   ****/
// axios
import axios from 'axios/index'
import M from '../assets/js/common';
import router from '../router/index'
//1. axios
const service = axios.create({
    // --
    baseURL: process.env.BASE_API,
    //  ms30s
    timeout: 3 * 10000
});
// 2.
service.interceptors.request.use(config => {
    //token,loading
    // config.data = JSON.stringify(config.data); //,qs
    // config.headers = {
    //     'Content-Type':'application/x-www-form-urlencoded' //
    // }
    //tokencookielocalStoragejs-cookie
    // const token = M.getCookie('mallToken');//tokentoken,
    // if(token){
    //     // config.params = {'token':token} //
    //     config.headers.token= token; //
    // }
    return config
}, error => {
    Promise.reject(error)
});

// 3.
service.interceptors.response.use(response => {
    //loading
    if(response.data.code === 3){
        console.log(response.data);
        M.removeLoading();
        // M.showText('');
        // location.href = process.env.login;
        router.push('/login');
        return false;
    }else{
        console.log(response.data);
        return response.data
    }
}, error => {
    /*****  *****/
    if (error && error.response) {
        // 1.
        // 2.
        let showText = '';
        switch (error.response.status) {
            case 400:
                showText = '';
                break;
            case 401:
                showText = '';
                break;
            case 403:
                showText = '';
                break;
            case 404:
                showText = ',';
                break;
            case 405:
                showText = '';
                break;
            case 408:
                showText = '';
                break;
            case 500:
                showText = '';
                break;
            case 501:
                showText = '';
                break;
            case 502:
                showText = '';
                break;
            case 503:
                showText = '';
                break;
            case 504:
                showText = '';
                break;
            case 505:
                showText = 'http';
                break;
            default:
                showText = ``;
        }
        M.showText(showText);
    } else {
        // 
        console.log(error);
        if (JSON.stringify(error).includes('timeout')) {
            M.showText('');
        }
        console.log('')
    }
    M.removeLoading();
    console.log(error.message);
    /*****  *****/
    //
    return Promise.resolve(error.response)
});
//4.
export default service



